/* 拽专 砖 转专 */
.date-box {
  display: flex;
  flex-direction: row; /* 砖专 转 */
  gap: 12px;
  align-items: center;
  position: relative;
  z-index: 10; /* 注 转  注 专住 */

}

/* 砖转 拽 */
.date-box input {
  min-width: 160px;
  padding: 10px 14px;
  border-radius: 8px;
  background: rgba(0, 0, 0, 0.85);
  color: #fff;
  font-size: 0.95rem;
  transition: all 0.3s ease;
}

/* 专住转 专专转  砖 react-datepicker */
.react-datepicker__input-container input {
  border: none !important;
  box-shadow: none !important;
}

/* 驻拽住 */
.date-box input:focus {
  outline: none !important;
  border: none !important;
  box-shadow: 0 0 8px rgba(224, 163, 255, 0.6); /* Glow 注 */
}

/* 驻转专  */
.date-box button {
  padding: 10px 16px;
  border-radius: 10px;
  border: none;
  background: linear-gradient(135deg, #e0a3ff, #ff69b4);
  color: #000;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
}

.date-box button:hover {
  background: linear-gradient(135deg, #00ffff, #ff0080);
  color: #dd0e9f;
  transform: scale(1.05);
}

/*  react-datepicker */
.react-datepicker {
  background: #222 !important; /*  */
  border-radius: 12px !important;
  border: 1px solid rgba(224, 163, 255, 0.3);
  color: #dd0e9f !important;
  box-shadow: 0 4px 20px rgba(0,0,0,0.6);
}

/* 转专转 砖 */
.react-datepicker__current-month {
  font-size: 1rem !important;
  font-weight: 600;
  color: #e509d6 !important;
}

/* 砖转  */
.react-datepicker__day-name {
  color: #e0a3ff !important;
}

/*  专 */
.react-datepicker__day {
  color: #fff !important;
  font-size: 0.9rem;
}

/* 住驻"砖 */
.react-datepicker__day--weekend {
  color: #ff69b4 !important;
}

/*  专 */
.react-datepicker__day--selected,
.react-datepicker__day--keyboard-selected {
  background: linear-gradient(135deg, #ff69b4, #e0a3ff) !important;
  color: #000 !important;
  border-radius: 6px;
}

/*  转  砖专 */
.react-datepicker__day--in-range,
.react-datepicker__day--in-selecting-range {
  background: linear-gradient(135deg, #ff69b4, #e0a3ff) !important;
  color: #000 !important;
  border-radius: 6px;
}

/* Hover 注  */
.react-datepicker__day:hover {
  background: linear-gradient(135deg, #ff69b4, #e0a3ff) !important;
  color: #000 !important;
  border-radius: 6px;
}

/* 拽 注 注 砖 砖 */
.react-datepicker__header {
  background:  #222  !important;
  border-bottom: none !important;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
  padding-top: 8px;
  padding-bottom: 8px;
  }
